* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  font-family: Roboto, sans-serif;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 80px;
  padding: 0 20px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  top: 10px;
  left: 50px;
}

.nav-list {
  gap: 20px;
  list-style: none;
  display: flex;
}

.nav-link {
  color: #010101;
  text-align: center;
  height: 50px;
  font-size: 14px;
  line-height: 50px;
  text-decoration: none;
}

.nav-link:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #33c1ff;
  width: 100%;
  height: 2px;
  display: block;
  position: relative;
  top: 5px;
}

.nav-link:hover:after {
  content: "";
  background-color: #00acdc;
  width: 100%;
  height: 1px;
  display: block;
  top: 10px;
}
